const Portfolio = ({ setBlockScroll }) => {
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [projectItemInfo, setProjectItemInfo] = useState({});

    useEffect(() => {
        if (modalIsOpen) {
            setBlockScroll(true);
        } else {
            setBlockScroll(false);
        }
    }, [modalIsOpen])

    function openModal() {
        setModalIsOpen(true);
    }

    function closeModal() {
        setModalIsOpen(false);

    }

    return (
        <section
            className='portfolio-wrapper'>

            <div className='portfolio-wrapper-content'>
                <h1 className='main-text'>PROJECTS</h1>
                <Swiper
                    slidesPerView={4}
                    spaceBetween={30}
                    centeredSlides={true}
                    // grabCursor={true}
                    pagination={{
                        clickable: true,
                    }}
                    modules={[Pagination]}
                    className="mySwiper"
                    breakpoints={{
                        0: {
                            slidesPerView: 1,
                            spaceBetween: 15,
                            centeredSlides: true,
                        },
                        500: {
                            slidesPerView: 2,
                            spaceBetween: 20,
                            centeredSlides: true,
                        },
                        1024: {
                            slidesPerView: 4,
                            spaceBetween: 30,
                            centeredSlides: true,
                        }


                    }}
                >

                    {
                        portfolioArr.map((item, index) => {

                            const contentSplit = item.skill.split(',');

                            return (
                                <SwiperSlide
                                    onClick={() => {
                                        setProjectItemInfo(item);
                                        openModal();
                                    }}
                                    key={index}
                                    style={{ display: 'flex', flexDirection: 'column' }}
                                >
                                    <h5 className='seprator'> </h5>
                                    <div className='content-wrap'>
                                        <section className='ima-wrap'>
                                            <img style={{ width: 200 }} src={item.image} />
                                        </section>
                                        <div className='seprator' />
                                        <section>
                                            <h3>[{item.title}]</h3>
                                        </section>
                                        <section className='skill-wrap'>
                                            <div style={{ height: 10 }} />
                                            <h5> </h5>
                                            <div>{contentSplit.map((item, index) => {
                                                return <div key={index}>{item}</div>
                                            })}
                                            </div>
                                        </section>
                                    </div>
                                </SwiperSlide>
                            )
                        })
                    }
                </Swiper>

            </div>
            <ModalComponent isOpen={modalIsOpen} onRequestClose={closeModal} projectItemInfo={projectItemInfo} />
        </section>
    )
}

export default Portfolio;